@import '../base.scss';

.c-blog {
  position: relative;
  padding-top: t-to-rem(130);
  z-index: 0;

  &.c-blog--homepage {
    padding-top: 0;
    @include t-gutters;

    .c-blog__heading {
      color: $s-color--font-1;

      @include t-media-query($s-small-down) {
        @include t-font--heading-7;
        margin-bottom: 1.25rem;
      }
    }


    .c-blog__image {
      position: relative;
      overflow: hidden;
      padding-bottom: t-to-rem(40);
      @include t-media-query($s-small-up) {
        display: none;
      }
      &-background {
        @include t-absolute-full;

        &-container {
          padding-bottom: 103.6%;
        }
      }

      &-overlay {
        object-fit: cover;
        width: auto;
        height: 100%;

        &-container {
          width: auto;
          height: 100%;
          @include t-absolute-center-x-y;
          height: 70%;
        }
      }
    }

    .c-blog__view-btn-container {
      display: none;
      flex-direction: row;
      justify-content: center;
      margin-top: t-to-rem(40);
      @include t-media-query($s-medium-down) {
        display: flex;
      }
    }
    .c-blog__articles {
      @include t-media-query($s-medium-down) {
        display: flex;
        overflow: hidden;
        column-gap: 0;
        row-gap: 0;
        .c-blog__article-author, .c-blog__article-cta {
          display: none;
        }
        .c-blog__article-image-container {
          margin-top: auto;
        }
      }
    }

    .c-blog__article {
      margin-bottom: 0;
    }

    .c-blog__article-tags:empty {
      display: none;
    }
    .c-blog__article-author {
      margin-top: auto;
    }
    .c-blog__article-image-container {
      margin-top: 0;
    }
  }

  &__articles-slider {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    &__dots{
      display: flex;
      justify-content: center;
      margin-top: t-to-rem(20);
    }

    &__dot{
      @include t-width-and-height(9);
      margin-right: t-to-rem(24);
      cursor: pointer;
      background-color: $s-color--font-1;
      opacity: 0.2;
      border-radius: 100%;
      &.is-active{
        opacity: 1;
      }
      &:last-of-type{
        margin-right: 0;
      }
    }
    @include t-media-query($s-medium-up) {
      &__dots {
        display: none;
      }
    }
  }

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: t-to-rem(414);
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__inner {
    @include t-gutters;
  }

  &__heading {
    color: $s-color--font-6;
    text-align: center;
    margin-bottom: 3.125rem;
  }

  &__header-article {
    position: relative;
    overflow: hidden;
    display: block;
    margin-bottom: t-to-rem(64);

    &-image {
      @include t-absolute-center-x-y;

      &.is-desktop {
        display: none
      }

      &-container {
        padding-bottom: 148%;
      }
    }

    &-inner {
      @include t-absolute-center-x-y;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-top {
      display: flex;
      align-items: center;

      .divider {
        border-left: t-to-rem(2) solid $s-color--white;
        margin: 0 t-to-rem(6);
        height: t-to-rem(12);
        overflow: hidden;
        display: inline-block;
      }
    }

    &-top,
    &-tag,
    &-author,
    &-title,
    &-cta {
      color: $s-color--font-6;
      text-align: center;

      &::before {
        background-color: $s-color--white;
      }
    }

    &-cta {
      font-weight: $s-font--weight-bold;

      svg {
        height: t-to-rem(10);
        margin-left: t-to-rem(6);

        path {
          stroke: $s-color--background;
        }
      }
    }
  }

  &__filter-btn {
    display: inline-block;
    height: 0;
    width: 100%;
    padding-bottom: 98%;
    border-radius: 50%;
    border: 0.0625rem solid $s-color--aqua;
    position: relative;

    &.is-selected {
      background-color: $s-color--aqua;
    }

    &-container {
      width: 29.68%;
      line-height: 0;
      margin-bottom: 0.655rem;
    }

    &-text {
      position: relative;
      width: 100%;
      height: 100%;

      &-span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__tags {
    margin-bottom: 3.0625rem;
    display: flex;
    justify-content: flex-start;

    &-container {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
    }

    &-label {
      margin-right: t-to-rem(4);
    }

    &-select {
      option {
        &[selected] {
          background-color: $s-color--background-tertiary;
        }
      }
    }
  }

  &__article {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    margin-bottom: 3.875rem;

    &-tags {
      margin-bottom: 0.6875rem;
      flex: 1;
    }

    &-tag {
      margin-right: t-to-rem(4);

      &:last-child {
        margin-right: unset;
      }
    }

    &-title {
      margin-bottom: 0.8125rem;
      line-height: 1.625rem;
    }

    &-author {
      margin-bottom: 1.8125rem;
      color: $s-color--font-3;
    }

    &-image {
      &-container {
        margin: auto 0 t-to-rem(20);
        width: 100%;
        padding-bottom: 98%;
      }
    }

    &-cta {
      font-weight: $s-font--weight-bold;

      &.o-link.has-arrow {
        color: $s-color--font-1;
      }
    }

    &:hover {
      .c-blog__article-cta::before {
        width: 100%;
      }
    }
  }

  @include t-media-query($s-small-down) {
    &__header-article-inner {
      justify-content: space-between;
      height: 100%;
      padding: t-to-rem(50) t-to-rem(30);
      width: 100%;
    }
  }

  @include t-media-query($s-small-up) {
    padding-top: 13.2%;

    &__header-background {
      padding-bottom: 37.1%;
      max-height: t-to-rem(742);
    }

    &__header-article {
      margin-bottom: 10.8%;

      &-image {
        &.is-mobile {
          display: none
        }

        &.is-desktop {
          display: block
        }

        &-container {
          padding-bottom: 73%;
        }
      }

      &-top {
        margin-bottom: t-to-rem(36);
      }

      &-title {
        margin-bottom: t-to-rem(20);
      }
    }

    &__tags {
      margin-bottom: t-to-rem(60);

      &-label {
        margin-right: t-to-rem(6);
      }
    }

    &__articles {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: t-to-rem(60);
      row-gap: t-to-rem(96);
    }

    &__article {
      margin-bottom: 0;

      &-tags {
        margin-bottom: t-to-rem(22);
      }

      &-title {
        margin-bottom: t-to-rem(26);
      }

      &-author {
        margin-bottom: t-to-rem(50);
      }

      &-image-container {
        margin-bottom: t-to-rem(44);
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__header-article {
      margin-bottom: 6%;
    }

    &__header-article-image-container {
      padding-bottom: 45%;
    }

    &__filter-btn {
      &-container {
        width: 10.81%;
      }
    }

    &__articles {
      grid-template-columns: repeat(3, 1fr);
    }

    &__article {
      &-title {
        line-height: 2.5rem;
        margin-bottom: 1.5rem;
      }

      &-tags {
        margin-bottom: 1.75rem;
      }
    }

    &__tags {
      margin-bottom: 7.125rem;

      &-label {
        margin-right: t-to-rem(8);
      }
    }

    .o-pagination {
      margin-top: 5rem;
    }
  }

  @include t-media-query($s-large-up) {
    &__heading {
      margin-bottom: 5rem;
    }

    &__articles {
      column-gap: 6rem;
      row-gap: 10.125rem;

    }
  }

  @include t-media-query($s-xxlarge-up) {
    padding-top: t-to-rem(264);
  }
}